___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --cell-water-color: #454d60;
  --text-color: #82b8ff;
  --bg-color: #010619;
  --cell-human-color: #027760;
  --cell-comp-color: #7b5d5d;
  --cell-water-color: #089feb;
}

body {
  background-color: var(--bg-color);
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: var(--text-color);
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: #ac5d03;
  font-size: 3rem;
}

.twoBoards {
  width: 80%;
  display: flex;
  justify-content: center;
  gap: 10%;
  color: var(--text-color);
}

.boardContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2vh;
}

.boardName {
  font-size: 1.5rem;
  font-weight: 600;
}

.grid {
  display: grid;
  grid-template-rows: repeat(10, 40px);
  grid-template-columns: repeat(10, 40px);
  gap: 5px;
}

.cell {
  background-color: var(--cell-human-color);
  text-align: center;
  font-size: 1rem;
}

.computerGrid .cell {
  background-color: var(--cell-comp-color);
}

.cell:hover {
  cursor: pointer;
  background-color: #636c82;
}

/* .computerGrid .ship {
  background-color: var(--cell-human-color);
} */

.humanGrid .ship {
  background-color: black;
}
